}

.grid_block-5 {
  background: linear-gradient(to right, transparent 0, rgb(105, 105, 105) 0), url(/img/block-5.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  grid-column: 1/5;
  grid-row: 3/3;
  border-radius: 100px;
}

.grid_block-5:hover {
  background: linear-gradient(to right, transparent 100%, rgba(105, 105, 105, 0.2) 0), url(/img/block-5.jpg);
  background-size: cover;
  background-repeat: no-repeat;
}

.grid_block-6 {
  grid-column: 5/9;
  grid-row: 3/3;
  border-radius: 100px;
  background: linear-gradient(to right, transparent 0, rgb(105, 105, 105) 0), url(/img/block-6.jpg);
  background-size: cover;
  background-repeat: no-repeat;
}

.grid_block-6:hover {
  background: linear-gradient(to right, transparent 0, rgba(105, 105, 105, 0.2) 0), url(/img/block-6.jpg);
  background-size: cover;
  background-repeat: no-repeat;
}

.grid__hover {
  -webkit-filter: grayscale(100%);
  filter: grayscale(100%);
  transform: scale(1, 1);
  transition: transform 0.3s ease-in-out;
}

.grid__hover:hover {
  -webkit-filter: grayscale(0);
  filter: grayscale(0);
  transform: scale(1.05, 1.05);
  transition: transform 0.3s ease-in-out;
}

