/* Переливашка */
.posicion_con {
  position: relative;
}

:root {
  --backgroundColor: #232323;
  --firstColor: #202020;
  --secondColor: wheat;
  --thirdColor: #FF5900;
  --fourthColor: dimgrey;
  --dropShadowColor: rgba(0, 0, 0, .7);
}

.main {
  display: grid;
  width: 100vw;
  height: 100vh;
}

.blob {
  margin: auto;
  position: absolute;
  z-index: 100;
  top: -60px;
  right: 300px;
}

.svg {
  transform-origin: 50% 50%;
  animation: move 24s ease-in-out infinite;
}

@keyframes move {
  0% {
    transform: scale(1) translate(0, 0) rotate(0);
    fill: var(--firstColor);
    filter: drop-shadow(20px 14px 10px var(--dropShadowColor));
  }
  25% {
    transform: scale(1.2) translate(5vw, 10vh) rotate(55deg);
    fill: var(--secondColor);
    filter: drop-shadow(14px 14px 6px var(--dropShadowColor));
  }
  50% {
    transform: scale(0.8) translate(15vw, 5vh) rotate(180deg);
    fill: var(--thirdColor);
    filter: drop-shadow(-16px -16px 16px var(--dropShadowColor));
  }
  75% {
    transform: scale(1.2) translate(5vw, 10h) rotate(55deg);
    fill: var(--fourthColor);
    filter: drop-shadow(14px 14px 6px var(--dropShadowColor));
  }
  100% {
    transform: scale(1) translate(0, 0) rotate(0);
    fill: var(--firstColor);
    filter: drop-shadow(20px 14px 10px var(--dropShadowColor));
  }
}
