.general {
  position: relative;
  z-index: 2;
}

.general-fon {
  background-color: dimgrey;
}

.general-circle {
  position: absolute;
  background-color: dimgrey;
  width: 300px;
  height: 300px;
  border-radius: 50%;
  /* не забываем о кроссбраузерности */
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  -khtml-border-radius: 50%;
  top: -61px;
  left: 620px;
  z-index: 1;
}

.general_info {
  position: relative;
  display: flex;
  flex-direction: row;
  z-index: 200;
  justify-content: space-between;
  position: relative;
  max-width: 100%;
  padding: 70px;
}

.general_info-block {
  display: flex;
  flex-direction: column;
  z-index: 200;
  width: 617px;
}

.general_info-zag {
  font-style: normal;
  font-weight: 800;
  font-size: 70px;
  line-height: 20px;
  text-align: left;
  color: #202020;
  margin-bottom: 35px;
}

.general_info-text {
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 40px;
  text-align: left;
  color: wheat;
}

.general_img {
  background-image: url(/img/grafit.png);
  background-repeat: no-repeat;
  background-size: cover;
  width: 516px;
  height: 400px;
  background-repeat: no-repeat;
  background-size: cover;
}

.section_bottom {
  padding-bottom: 60px;
}

.footer_up:hover svg {
  transform: scale(1.1, 1.1);
  transition: transform 0.3s ease-in-out;
}

.telegram-button {
  position: fixed;
  right: 13px;
  bottom: 90px;
  transform: translate(-50%, -50%);
  background: #0088cc;
  /*цвет кнопки*/
  border-radius: 50%;
  width: 55px;
  /*ширина кнопки*/
  height: 55px;
  /*высота кнопки*/
  color: #fff;
  text-align: center;
  line-height: 53px;
  /*центровка иконки в кнопке*/
  font-size: 35px;
  /*размер иконки*/
  z-index: 9999;
}

.telegram-button a {
  color: #fff;
}

.telegram-button:before,
.telegram-button:after {
  content: " ";
  display: block;
  position: absolute;
  border: 50%;
  border: 1px solid #0088cc;
  /*цвет анимированных волн от кнопки*/
  left: -20px;
  right: -20px;
  top: -20px;
  bottom: -20px;
  border-radius: 50%;
  animation: animate 1.5s linear infinite;
  opacity: 0;
  backface-visibility: hidden;
}

.telegram-button:after {
  animation-delay: 0.5s;
}

.fa {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: flex-end;
  padding-top: 13px;
}

@keyframes animate {
  0% {
    transform: scale(0.5);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: scale(1.2);
    opacity: 0;
  }
}
